import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { stipulateAmountOfScholarship } from 'dump/data';

const columns = [
  {
    title: 'Xếp loại',
    dataIndex: 'grad',
    key: 'grad',
  },
  {
    title: 'Mã xếp loại',
    dataIndex: 'gradCode',
    key: 'gradCode',
  },
  {
    title: 'Hệ',
    dataIndex: 'sys',
    key: 'sys',
  },
  {
    title: 'Từ điểm HT 10',
    dataIndex: 'fromScores10',
    key: 'fromScores10',
  },
  {
    title: 'Từ điểm HT 4',
    dataIndex: 'fromScores4',
    key: 'fromScores4',
  },
  {
    title: 'Từ điểm RL',
    dataIndex: 'fromTrainingScore',
    key: 'fromTrainingScore',
  },
  {
    title: 'Số tiền',
    dataIndex: 'amountOfMoney',
    key: 'amountOfMoney',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      stipulateAmountOfScholarship.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#scholarship/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class StipulateAmountOfScholarshipPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Quy định xếp loại học bổng">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/scholarship/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={stipulateAmountOfScholarship} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StipulateAmountOfScholarshipPage />
  </Page>
);



